.riddlePopUpBackground {
    position: fixed;
    top: 0;
    left: 0;

    padding: 0;
    margin: 0;

    width: 100%;
    height: 100%;

    background: rgba(0, 0, 0, 0.0);
    animation: fade .3s ease forwards ;
    z-index: 2;

    @keyframes fade {
        to {
        background: rgba(0, 0, 0, 0.5);
        } 
    }

    display: flex;
    flex-direction: column;

    .riddlePopUp{
        position: relative;
        max-width: 50%;
        max-height: 90%;
        margin: auto;
        background: url('../media/christmas_bg.webp') no-repeat center center;
        border-radius: $borderRadius;
        box-shadow: 0 10px 10px #333;
        cursor:default;
        animation: popup .6s ease forwards;
        transform: scale(0);
        transform-origin: center;
        display: flex;
        flex-direction: column;

        .riddleContent {
            position: relative;
            text-align: center;
            color: $white;
            max-height: 100%;
            padding: 2rem;
            overflow-y: auto;
            scrollbar-width: none;
            display: flex;
            flex-direction: column;
            white-space: pre-line;
    
            @keyframes popup {
                to {
                    transform: scale(1);
                } 
            }
        
    
            h2 {
                margin: 1rem 0;
                margin-bottom: 1rem;
                font-size: 3rem;
                font-family: $font-5
            }
        
            .text {
                margin: auto 1rem;
                padding-bottom: 1rem;
                color: $white;
                font-size: 1.5rem;
                font-family: $font-44
            }
        
            img{
                margin: 0 auto;
                padding: 1rem;
                width: 90%;
                max-width: 500px;
            }
        
            input{
                font-family: $font-44;
                font-size: 1.3rem;
                padding-left: 5px;
                max-width: 100%;
                margin: 1rem auto;
            }
        
            .button {
                margin-top: 1rem;
                width: 120px;
                cursor: pointer;
                font-size: 1.5rem;
                font-family: $font-5;
                background: $white;
                border-radius: 30px;
                padding: 1rem;
                transition: .2s;
                margin-left: auto;
                margin-right: auto;
                border: 2px solid $color-2;
            }
            .white{
                color: $color-1;
                background: $white;
                border-color: $color-2;
        
                &:hover {
                    background: $color-4;
                }
            }
            .green{
                color: $white;
                background: $color-3;
                border-color: $white;
            }
            .red{
                color: $white;
                background: $color-5;
                border-color: $color-1;
            }
        }

        @media (max-width: 520px){
            max-width: 90% !important;
            .riddleContent{                
                h2{
                    font-size: 2.5rem;
                }
        
                .text{
                    font-size: 1rem;
                }
            }
        }

        .spu-close-popup {
            font-size: 30px;
            color: $white;
            text-shadow: 0 1px 1px #000;
            position: absolute;
            cursor: pointer;
            background: transparent;
            border: 0;
            line-height: .5;
            text-decoration: none;
            right: 15px;
            top: 18px;
            z-index: 100;
        }
    
        .spu-close-popup:hover{
            text-shadow: 0 2px 2px #000;
        }

    }

    .riddlePopUp::after {
        content: '';
        display: block;
        position: absolute;
        top: -36px;
        left: 50%;
        transform: translate(-50%);
        height: 150px;
        width: 600px;
        background: url('../media/icicle.svg') no-repeat 50% 0;
        background-size: 50%;
        // background: url(data:image/png;base64,) no-repeat 0 0,
        // url(data:image/png;base64,) no-repeat 50% 0,
        // url(data:image/png;base64,) no-repeat 100% 0;
        
        @media (max-width: 520px){
            top: -21px;
            height: 40%;
            width: 100%;
        }
    }


}